import React, { useState } from "react";
import { createRoot } from "react-dom/client";
import Popup from "./Popup";

// Importa los estilos del modo claro y oscuro
import "./index-light.css";
import "./index-dark.css";

const container = document.getElementById("app-container");

function App() {
	const [isDarkMode, setIsDarkMode] = useState(false);

	// Función para alternar entre el modo oscuro y claro
	const toggleDarkMode = () => {
		setIsDarkMode(!isDarkMode);
	};

	// Aplica la clase "dark" o "light" al contenedor según el modo
	const containerClass = isDarkMode ? "dark" : "light";

	return (
		<div id="app-container" className={containerClass}>
			<Popup />
		</div>
	);
}

// Crea el root y renderiza el componente App
const root = createRoot(container);
root.render(<App />);
